.todo{
    margin-top: 20px;
    width: 400px;
    min-height: 500px;
    max-height: 500px;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    padding: 5px;
    background-image: linear-gradient(to bottom, white, black);
    box-shadow: 0px 0px 20px black;
}
.list-todo::-webkit-scrollbar{
    display: none;
}
.todo-content{
    width: 100%;
    min-height: calc(100% - 100px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.title{
    padding: 15px;
    text-align: center;
    min-height: 30px;
    max-height: 100px;
}
.todo-input{
    flex: 0 0 auto;
    width: 100%;
    padding: 10px;
    max-height: 50px;
    min-height: 50px;
}
.todo-input input{
    width: 88%;
    height: 30px;
    border-top: 0px;
    border-right: 0px;
    border-left: 0px;
    outline: 0px;
    padding: 5px;
    background-color: rgba(240, 248, 255, 0);
}
.todo-input input:focus{
    border-bottom: 2px solid black;
}
.todo-input button{
    width: 10%;
    height: 30px;
    margin-left: 2%;
    background-color: green;
    color: white;
    border: 0px;
    outline: 0px;
    cursor: pointer;
    font-size: 20px;
    transition: all 0.5s linear;
}
.todo-input button:hover{
    background-color: rgb(0, 163, 0);
    color: aliceblue;
}
.complete-show{
    height: 50px;
    color: greenyellow;
    display: flex;
    justify-content: center;
    align-items: center;
}
.list-todo{
    height: 320px;
    overflow-y: scroll;
}
.list-todo span{
    margin-right: 5px;
}
.list-todo button{
    margin-right: 5px;
}
.item-todo{
    display: flex;
    border-bottom: 2px solid;
}
.item-todo .checkbox {
    flex: 1 0 auto;
    min-width: 50%;
    max-width: 50%;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.item-todo .checkbox label{
    margin-left: 5px;
    text-shadow: 0px 0px 10px white, 0px 0px 20px blueviolet;
    cursor: pointer;
}
.item-todo button {
    flex: 1 0 auto;
    min-width: 10%;
    padding: 5px;
    border: 0px;
    outline: 0px;
    cursor: pointer;
}
.item-todo button.button-edit{
    background-color: blue;
    color: white;
    transition: all 0.5s linear;
}
.item-todo button.button-edit:hover{
    background-color: rgb(2, 2, 213);
}
.item-todo button.button-delete{
    background-color: red;
    color: white;
    transition: all 0.5s linear;
}
.item-todo button.button-delete:hover{
    background-color: rgb(163, 0, 0);
    color: yellow;

}
.item-todo button.button-complete{
    background-color: greenyellow;
    color: white;
    transition: all 0.5s linear;
}
.item-todo button.button-complete:hover{
    background-color: rgb(3, 167, 3);
}
.complete{
    text-decoration: line-through;
}
.icon-complete{
    color: green;
}
.modal{
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: greenyellow;
    color: black;
    padding: 10px;
    border-radius: 15px;
}
.modal .exit{
    cursor: pointer;
    display: flex;
    flex-direction: row-reverse;
    font-size: 20px;
}
.icon {
    font-size: 20px;
    margin-right: 5px;
    display: block;
    text-align: center;
    height: 50px;
    font-size: 35px;
    color: green;
}
.modal p {
    min-height: 20px;
}
.copyright{
    position: fixed;
    top: 0;
    left: 0;
}
.copyright h1{
    text-indent: 10px;
}
.copyright h3{
    text-indent: 100px;
}

@media screen and (max-width: 500px){
    .modal{
        width: 200px;
    }
}

